
.home-top {
	background: {
		image: url('/images/home-header.webp'), $bg;
		repeat: no-repeat;
		position: 14% bottom;
		size: 130%;
	}

	.safari &,
	.ie & {
		background: {
			image: url('/images/home-header.png'), $bg;
		}
	}

	@include b-up(sm) {
		background: {
			position: right bottom;
			size: contain;
		}
	}

	&__content {
		@extend %col-12;
		@extend %col-md-8;
	}

	&__buttons {
		@extend %d-flex;
		@extend %flex-column;
		@extend %flex-md-row;
		margin: {
			top: 48px;
		}

		.btn {
			+ .btn {
				@include b-up(md) {
					margin: {
						left: indents(xxl);
					}
				}

				@include b-down(sm) {
					margin: {
						top: indents(lg);
					}
				}
			}
		}
	}
}
