@use 'sass:math';

/* stylelint-disable */
@import '../../../scss/util/variables';
@import '../../../scss/util/functions';
@import '../../../scss/util/mixins';
@import '../../../scss/util/placeholder';
/* stylelint-enable */















.wrapper {
	@extend %d-flex;
	@extend %align-items-center;
	@extend %justify-content-center;

	position: absolute;
	z-index: 20;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background: {
		color: rgba($white, .6);
	}
}

.Loader {
	display: inline-block;
	position: relative;
	width: 80px;
	height: 80px;
	transform: scale(.6);

	&__circle {
		transform: {
			origin: 40px 40px;
		}
		animation: {
			name: lds-roller;
			duration: 1200ms;
			timing-function: cubic-bezier(.5, 0, .5, 1);
			iteration-count: infinite;
		}

		&::after {
			content: ' ';
			display: block;
			position: absolute;
			width: 7px;
			height: 7px;
			margin: {
				top: -4px;
				right: 0;
				bottom: 0;
				left: -4px;
			}
			border: {
				radius: 50%;
			}
			background: {
				color: $violet;
			}
		}

		$arr:
			1 -36ms 63px 63px,
			2 -72ms 68px 56px,
			3 -108ms 71px 48px,
			4 -144ms 72px 40px,
			5 -180ms 71px 32px,
			6 -216ms 68px 24px,
			7 -252ms 63px 17px,
			8 -288ms 56px 12px;

		@each $ind, $delay, $top, $left in $arr {
			&:nth-child(#{$ind}) {
				animation: {
					delay: $delay;
				}

				&::after {
					top: $top;
					left: $left;
				}
			}
		}
	}
}

@keyframes lds-roller {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}

