@use 'sass:math';

/* stylelint-disable */
@import '../../../scss/util/variables';
@import '../../../scss/util/functions';
@import '../../../scss/util/mixins';
@import '../../../scss/util/placeholder';
/* stylelint-enable */






















































.map {
	position: relative;
	z-index: 1;
	width: 100%;
	height: 414px;

	@include b-down(sm) {
		filter: drop-shadow(0 6px 20px rgba(0, 0, 0, .15));
	}

	@include b-up(md) {
		height: 500px;
	}
}

.icon {
	width: 44px;
	height: 44px;
}
