@mixin hover($type: 'main') {
	@if $type == 'bg' {
		@include transition('background, border');
	}

	@else if $type == 'border' {
		@include transition('border, color');
	}

	@else {
		@include transition(color);
	}

	@include b-up(md) {
		&:hover {
			@if $type == 'bg' {
				border: {
					color: $yellow;
				}
				background: {
					color: $yellow;
				}
			}

			@else if $type == 'border' {
				border: {
					color: $yellow;
				}

				color: $yellow;
			}

			@else {
				color: $yellow;
			}

			@content;
		}
	}
}
