.content {
	@include font(md, 'Raleway', 300);

	position: relative;
	width: 100%;
	max-width: 100%;
	color: $gray;
	line-height: 1.2;

	p {
		+ * {
			margin: {
				top: indents(md);
			}
		}

		+ address {
			margin: {
				top: indents(md);
			}
		}
	}

	b {
		font: {
			weight: 600;
		}
	}

	ul {
		+ * {
			margin: {
				top: indents(md);
			}
		}

		li {
			position: relative;
			margin: {
				bottom: indents(xs);
			}
			padding: {
				left: 16px;
			}

			&::before {
				content: '';
				position: absolute;
				top: 7px;
				left: 0;
				width: 4px;
				height: 4px;
				border: {
					radius: 50%;
				}
				background: {
					color: $gray;
				}
			}
		}
	}

	ol {
		+ * {
			margin: {
				top: indents(md);
			}
		}

		counter-reset: counter;

		li {
			margin: {
				bottom: indents(xs);
			}

			counter-increment: counter;

			&::before {
				content: counter(counter) '. ';
			}
		}
	}
}
