
.partaking {
	.section__header {
		margin: {
			bottom: indents(xxl);
		}

		.headline {
			@include b-down(sm) {
				@include font(lg);
			}
			margin: {
				bottom: 0;
			}
		}
	}

	&__list {
		@extend %col-12;
	}

	&__item {
		@extend %flex-md-row-reverse;
		@extend %align-items-center;

		&:not(:last-child) {
			margin: {
				bottom: indents(xl);
			}
		}
	}

	&__item-text {
		@extend %col-12;
		@extend %col-md-9;
	}

	&__item-img {
		@extend %col-12;
		@extend %col-md-3;
		@extend %d-flex;
		@extend %justify-content-md-center;
		@extend %align-items-center;

		@include b-down(sm) {
			margin: {
				bottom: indents(xl);
			}
		}

		img {
			width: auto;
			max-width: 134px;
			height: auto;
			max-height: 100%;

			@include b-up(md) {
				max-width: 154px;
			}
		}
	}
}
