.list {
	margin: {
		top: indents(xxl);
		bottom: -30px;
	}

	&__item {
		@extend %col-12;
		@extend %col-md-4;
		@extend %d-flex;
		@extend %align-items-center;

		margin: {
			bottom: indents(xl);
		}

		&--clear {
			margin: {
				bottom: 0;
			}
		}

		&--large {
			@extend %col-md-6;
		}

		.icon {
			width: 42px;
			min-width: 42px;
			height: 42px;
			margin: {
				right: indents(md);
			}

			color: $yellow;
		}
	}

	&__item-text {
		@include font(md, 'Raleway');

		color: $gray;
	}

	+ .content {
		padding: {
			top: indents(xl);
		}
	}
}
