
.contacts {
	/* .headline {
		margin: {
			bottom: indents(xxl);
		}
	} */

	&__list {
		margin: 0;
		padding: {
			top: indents(lg);
			bottom: indents(lg);
		}

		&:not(:last-of-type) {
			@include border(bottom, $violet, 4);
		}
	}

	&__item {
		@extend %col-12;
		@extend %col-lg-4;

		padding: 0;

		&--full {
			@extend %col-lg-12;
			@extend %d-inline-flex;
			@extend %justify-content-center;
		}
	}

	&__link {
		@include hover;
		@include font(md, 'Montserrat');

		@extend %d-inline-flex;
		@extend %align-items-center;

		color: $violet;
		cursor: pointer;

		@include b-up(xs) {
			@include font(slg);
		}

		@include b-up(md) {
			@include font(lg);
		}

		@include b-down(md) {
			margin: {
				bottom: indents(lg);
			}
		}

		&--address {
			@extend %flex-column;
			@extend %flex-lg-row;
			@extend %justify-content-center;

			text: {
				align: center;
			}

			.icon {
				@include b-down(md) {
					margin: {
						bottom: indents(md);
					}
				}
			}
		}

		.icon {
			width: 46px;
			height: 46px;
			margin: {
				right: indents(xl);
			}
		}
	}

	.map {
		width: 100%;
		height: 414px;

		@include b-up(md) {
			height: 500px;
		}
	}
}
