@use 'sass:math';

/* stylelint-disable */
@import '../../../scss/util/variables';
@import '../../../scss/util/functions';
@import '../../../scss/util/mixins';
@import '../../../scss/util/placeholder';
/* stylelint-enable */

































































.popup {
	@extend %d-flex;
	@extend %align-items-center;
	@extend %justify-content-center;

	display: flex;
	position: fixed;
	z-index: 99;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	overflow: hidden;

	background: {
		color: hsla(0, 0%, 15%, .25);
	}

	&__close {
		@extend %d-flex;
		@extend %align-items-center;
		@extend %justify-content-center;

		position: absolute;
		z-index: 22;
		top: 20px;
		right: 30px;
		width: 40px;
		height: 40px;
		cursor: pointer;

		@include b-up(md) {
			top: 20px;
			right: 70px;
		}

		.icon {
			width: 22px;
			height: 22px;
		}
	}

	&__main {
		@include make-container;

		position: relative;
		max-width: 1180px;
		margin: {
			right: 0;
			left: 0;
		}

		&.is-small {
			max-width: 300px;
		}
	}

	&__content {
		max-height: 90vh;
		padding: 30px 20px;
		overflow: auto;
		border: {
			radius: 6px;
		}
		background: {
			color: $white;
		}

		@include b-up(md) {
			padding: 25px 60px;
		}
	}
}

