// colors
$white: hsl(0, 0%, 100%);
$dark: hsl(0, 0%, 20%);
$gray: hsl(0, 0%, 54%);
$gray-light: hsl(0, 0%, 95%);
$yellow: hsl(26, 78%, 77%);
$header: hsl(0, 0%, 18%);
$violet: hsl(262, 25%, 34%);

$bg: linear-gradient(179.11deg, $white -24.78%, hsl(260, 13%, 86%) 138.2%);
$bg-footer: linear-gradient(38.1deg, $white -55.01%, hsl(260, 13%, 86%) 145.82%);
$shadow: 0 2px 20px hsla(0, 0%, 0%, .1);

// допустимі значення шрифтів
$font: (
	xs: 9, // текст в елементах
	sm: 14, // бейджі, кнопки ...
	md: 16, // основний текст
	xmd: 18, // основний текст
	mlg: 20, // заголовки
	slg: 24, // заголовки
	lg: 26, // заголовки
	mxl: 36, // заголовки
	xl: 48, // заголовки
);

// допустимі значення зовнішніх і внутрішніх відступів
$indents: (
	xs: 5,
	sm: 10,
	md: 15,
	lg: 20,
	xl: 30,
	xxl: 40,
);

$grid-breakpoints: (
	xxs: 0,
	xs: 374px,
	sm: 640px,
	md: 961px,
	lg: 1200px,
	xl: 1599px,
	xxl: 1920px,
);
$grid-columns: 12;
$grid-gutter-width: 40px;

$time: 300ms; // час переходу
