.table {
	&__row {
		@include border(bottom, $gray-light);

		@extend %align-items-md-center;

		margin: 0;
		padding: {
			top: indents(md);
			bottom: indents(md);
		}
	}

	&__row-name {
		@include font(xmd, 'Raleway', 600);

		@extend %col-12;
		@extend %col-md-7;

		padding: 0;

		@include b-down(sm) {
			margin: {
				bottom: indents(sm);
			}
		}
	}

	&__row-label {
		@extend %col-md-4;

		padding: 0;
		color: $dark;

		font: {
			weight: 300;
		}

		@include b-down(sm) {
			margin: {
				bottom: indents(sm);
			}
		}
	}

	&__row-links {
		@extend %col-12;
		@extend %col-md-5;
		@extend %d-flex;
		@extend %justify-content-between;
		@extend %justify-content-md-end;

		padding: 0;

		.link {
			&:not(:last-child) {
				margin: {
					right: indents(lg);
				}

				@include b-up(md) {
					margin: {
						right: 50px;
					}
				}
			}
		}
	}

	&__row-value {
		@extend %col-12;
		@extend %col-md-8;

		padding: 0;

		@include b-up(md) {
			text: {
				align: right;
			}
		}
	}

	&__row-services {
		@extend %col-12;
		@extend %col-md-9;

		padding: 0;
	}
}
