.lang {
	@include font(md, 'Raleway', 600);

	position: relative;
	z-index: 2;

	&__btn {
		@include hover(bg);

		@extend %d-flex;
		@extend %align-items-center;
		@extend %justify-content-center;

		width: 60px;
		height: 34px;
		border: {
			radius: 4px;
		}

		background: hsl(0, 0%, 94%);
		cursor: pointer;

		.icon {
			@include transition(transform);

			width: 11px;
			min-width: 11px;
			height: 11px;
			margin: {
				left: indents(xs);
			}
		}
	}

	&__list {
		@include transition('opacity, top');

		position: absolute;
		top: 36px;
		right: 0;
		left: 0;
		overflow: hidden;
		opacity: 0;
		border: {
			radius: 4px;
		}

		pointer-events: none;
	}

	&__item {
		@include hover(bg);

		@extend %d-flex;
		@extend %align-items-center;
		@extend %justify-content-center;

		height: 34px;
		background: hsl(0, 0%, 94%);

		&:not(:last-child) {
			margin: {
				bottom: 1px;
			}
		}
	}

	&__control {
		&:checked {
			~ .lang__list {
				top: 36px;
				opacity: 1;
				pointer-events: all;
			}

			~ .lang__btn {
				.icon {
					transform: scale(1, -1);
				}
			}
		}
	}
}
