.btn {
	@include transition('opacity');
	@include border(all, $violet);
	@include font(md, 'Raleway', 600);

	position: relative;
	width: 260px;
	max-width: 100%;
	height: 40px;
	overflow: hidden;
	color: $violet;
	cursor: pointer;
	padding: {
		right: indents(xl);
		left: indents(xl);
	}
	border: {
		radius: 4px;
	}
	text: {
		align: center;
	}

	&--violet {
		@include hover(bg);
		background: {
			color: $violet;
		}

		color: $white;
	}

	&--white {
		@include hover(border);
	}
}

a {
	&.btn {
		@extend %d-inline-flex;
		@extend %align-items-center;
		@extend %justify-content-center;
	}
}
