@mixin font($s, $family: 0, $w: 0) {
	@if map-get($font, $s) {
		font: {
			size: #{map-get($font, $s)}px;
		}
	}

	@else {
		@error 'Розмір #{$s}px недопустимий';
	}

	@if $w > 0 {
		font: {
			weight: $w;
		}
	}

	@if $family != 0 {
		font: {
			family: $family;
		}
	}
}

@mixin font-build($arr, $path, $family, $style: normal) {
	@each $weight, $name in $arr {
		$src-woff2: url('#{$path}#{$name}.woff2') format('woff2');
		$src-woff: url('#{$path}#{$name}.woff') format('woff');

		@font-face {
			font: {
				display: swap;
				family: $family;
				weight: $weight;
				style: $style;
			}

			src: $src-woff2, $src-woff;
		}
	}
}
