$pathRaleway: '/fonts/Raleway/Raleway-';
$sizesRaleway: (
	900: 'Black',
	700: 'Bold',
	600: 'SemiBold',
	500: 'Medium',
	400: 'Regular',
	300: 'Light',
	100: 'Thin',
);
$pathMontserrat: '/fonts/Montserrat/Montserrat-';
$sizesMontserrat: (
	900: 'Black',
	700: 'Bold',
	600: 'SemiBold',
	500: 'Medium',
	400: 'Regular',
);

@include font-build($sizesRaleway, $pathRaleway, 'Raleway');
@include font-build($sizesMontserrat, $pathMontserrat, 'Montserrat');
