%mobile {
	@include b-down(md) {
		~ .header__mobile {
			justify-content: flex-end;

			.burger__menu {
				display: none;
			}

			.burger__close {
				display: flex;
			}
		}

		~ .header__main {
			position: fixed;
			top: 60px;
			right: 0;
			bottom: 0;
			left: 0;
			padding: indents(lg);

			.menu--top,
			.menu--main,
			.header__lang {
				display: flex;
			}

			.menu--callback {
				top: calc(-60px + 28px / 2);
				transform: scale(0);
			}

			.logo {
				top: calc(-60px + 32px / 2);
				left: 20px;
			}
			background: {
				color: $white;
			}
		}
	}
}

.header {
	position: relative;
	z-index: 2;
	box-shadow: $shadow;
	user-select: none;

	@include b-down(md) {
		.menu__link {
			&:not(.menu__link--phone) {
				font: {
					size: 18px;
				}
			}

			&--phone {
				font: {
					size: 20px;
				}

				position: relative;
				bottom: -20px;
				align-self: center;
			}
		}

		.menu--top,
		.menu--main,
		.header__lang {
			display: none;
		}

		.header__main {
			bottom: 100%;
			padding: 0;
		}
	}

	&__main {
		@include transition(top);

		@extend %d-flex;
		@extend %flex-column-reverse;
		@extend %flex-lg-column;
		@extend %justify-content-end;

		@include b-down(md) {
			position: fixed;
			top: 70px;
			right: 0;
			bottom: 0;
			left: 0;
			padding: indents(lg);
			background: {
				color: $white;
			}
		}
	}

	&__lang {
		@include b-down(md) {
			position: relative;

			.lang {
				position: absolute;
				top: calc(50% - 17px);
				right: 0;
			}
		}
	}

	&__top {
		@extend %d-flex;
		@extend %align-items-center;

		width: 100%;

		@include b-up(lg) {
			padding: {
				top: indents(sm);
				bottom: indents(sm);
			}
		}
	}

	&__bottom {
		@include b-up(lg) {
			@include border(top, hsl(0, 0%, 95%));
			padding: {
				top: indents(sm);
				bottom: indents(sm);
			}
		}

		@include b-down(md) {
			margin: {
				bottom: indents(lg);
			}
		}
	}

	&__bottom-container {
		@extend %d-flex;
	}

	&__bottom-menu {
		@extend %d-flex;
		@extend %align-items-center;
		@extend %justify-content-between;
	}

	&__mobile {
		@include border(bottom, hsla(262, 25%, 34%, .1));

		@extend %d-flex;
		@extend %d-lg-none;
		@extend %justify-content-start;

		@include b-down(md) {
			position: fixed;
			top: 0;
			right: 0;
			left: 0;
			height: 60px;
			background: {
				color: $white;
			}

			box-shadow: 0 4px 10px hsla(0, 0%, 0%, .15);

			+ .header__main {
				.logo {
					@include transition('top, left');

					position: absolute;
					top: calc(-70px + 32px / 2);
					left: calc(50% - 142px / 2);
				}
			}
		}
	}

	&__control {
		&:checked {
			@extend %mobile;
		}
	}
}
