.section {
	padding: {
		top: 38px;
		bottom: 38px;
	}

	@include b-up(md) {
		padding: {
			top: 82px;
			bottom: 82px;
		}
	}

	&__header {
		@extend %col-12;
		@extend %col-md-11;
	}

	&__content {
		@extend %col-12;
		@extend %col-md-9;
	}

	&--bg {
		background: {
			image: $bg;
		}
	}

	&--main {
		padding: {
			top: 50px;
			bottom: 80px;
		}

		@include b-up(md) {
			padding: {
				top: 146px;
				bottom: 146px;
			}
		}
	}

	&--small {
		padding: {
			top: 50px;
			bottom: 50px;
		}

		@include b-up(md) {
			padding: {
				top: 50px;
				bottom: 50px;
			}
		}
	}

	&--xs {
		padding: {
			top: 25px;
			bottom: 25px;
		}

		@include b-up(md) {
			padding: {
				top: 36px;
				bottom: 36px;
			}
		}
	}
}
