.fonds {
	.section__header {
		.headline {
			font: {
				size: 20px;
			}

			@include b-up(md) {
				font: {
					size: 30px;
				}
			}
		}
	}

	&__headline {
		margin: {
			top: indents(xl);
			bottom: indents(xl);
		}

		@include b-up(md) {
			margin: {
				top: 50px;
			}
		}
	}

	&__bottom {
		background: linear-gradient(157.3deg, hsl(0, 0%, 100%) 38.78%, hsl(260, 13%, 86%) 158.16%);
	}
}
