
.about {
	&__main {
		@extend %align-items-md-center;
		margin: {
			top: indents(xl);
		}

		@include b-up(md) {
			margin: {
				top: 60px;
			}
		}
	}

	&__info {
		@extend %col-12;
		@extend %col-md-5;

		@include b-down(sm) {
			margin: {
				bottom: indents(xl);
			}
		}
	}

	&__list {
		@extend %col-12;
		@extend %col-md-7;
		margin: {
			bottom: -50px;
		}
	}

	&__item {
		@extend %col-6;
		@extend %col-md-4;
		@extend %d-flex;
		@extend %flex-column;
		@extend %align-items-center;
		margin: {
			bottom: 50px;
		}

		.icon {
			width: 98px;
			height: 98px;
			margin: {
				bottom: indents(lg);
			}
		}
	}

	&__item-label {
		@include font(sm, 'Raleway');

		max-width: 100%;
		text: {
			align: center;
		}

		@include b-up(md) {
			@include font(md);
		}
	}
}
