@use 'sass:math';

/* stylelint-disable */
@import '../../../scss/util/variables';
@import '../../../scss/util/functions';
@import '../../../scss/util/mixins';
@import '../../../scss/util/placeholder';
/* stylelint-enable */















































.wrapper {
	@extend %d-block;

	position: relative;
	width: 100%;
}

.label {
	@include transition('font');

	position: absolute;
	z-index: 2;
	top: 10px;
	left: 0;
	color: hsl(0, 0%, 74%);
	white-space: nowrap;
	pointer-events: none;

	@include b-up(md) {
		@include font(xmd);
	}
}

.line {
	@include border(bottom, $violet, 2);
	@include transition('left, opacity');

	position: absolute;
	z-index: 2;
	right: 50%;
	bottom: 0;
	left: 50%;
	opacity: 0;
}

.error {
	position: absolute;
	bottom: -14px;
	left: 0;
	color: hsl(0, 100%, 65%);
	font: {
		size: 10px;
	}
}

.input {
	@include border(all, transparent);
	@include border(right, transparent);
	@include border(left, transparent);
	@include border(bottom, $gray-light);
	@include font(md, 'Raleway', 600);

	width: 100%;
	height: 40px;

	@include b-up(md) {
		@include font(xmd);
	}

	&--value,
	&:focus {
		outline: 0 none;

		+ .label {
			top: -10px;
			color: $violet;
			font: {
				size: 12px;
			}
		}

		~ .line {
			right: 0;
			left: 0;
			opacity: 1;
		}
	}
}
