%ch-collapsed {
	+ .collapsed__content {
		@extend %d-block;
	}

	~ .collapsed__btn {
		.collapsed__btn-controll {
			&::before {
				top: 6px;
				height: 0;
			}
		}

		.collapsed__btn-text {
			&::before {
				content: attr(data-text-close);
			}
		}
	}
}

.collapsed {
	display: block;

	.headline {
		color: $dark;
	}

	&__content {
		@extend %d-none;
		margin: {
			top: indents(md);
		}
		padding: {
			left: indents(lg);
		}

		@include b-up(md) {
			padding: {
				left: indents(xxl);
			}
		}
	}

	&__btn {
		@extend %d-inline-flex;
		@extend %align-items-center;

		margin: {
			top: indents(md);
		}

		cursor: pointer;
	}

	&__btn-controll {
		position: relative;
		width: 13px;
		height: 13px;
		margin: {
			right: 12px;
		}

		&::before,
		&::after {
			content: '';
			position: absolute;
			background: {
				color: $violet;
			}
		}

		&::before {
			@include transition('top height');

			top: 0;
			left: 6px;
			width: 1px;
			height: 13px;
		}

		&::after {
			top: 6px;
			left: 0;
			width: 13px;
			height: 1px;
		}
	}

	&__btn-text {
		@include font(md, 'Raleway', 300);

		color: $violet;

		&::before {
			content: attr(data-text-open);
		}
	}

	&__input {
		&:checked {
			@extend %ch-collapsed;
		}
	}
}
