.logo {
	@extend %d-block;

	width: 142px;
	height: 32px;

	&__img {
		@extend %d-block;

		width: 142px;
		height: 32px;
	}

	&--footer {
		width: 230px;
		height: 52px;

		@include b-up(lg) {
			width: 262px;
			height: 60px;
		}

		.logo__img {
			width: 230px;
			height: 52px;

			@include b-up(lg) {
				width: 262px;
				height: 60px;
			}
		}
	}
}
