.menu {
	@extend %d-flex;
	@extend %flex-column;
	@extend %flex-lg-row;
	@extend %flex-wrap;
	@extend %align-items-start;
	@extend %align-items-lg-center;
	@extend %justify-content-between;

	&__link {
		@include hover;

		@extend %d-inline-flex;
		@extend %align-items-center;

		color: $header;
		font: {
			weight: 500;
		}

		@include b-up(lg) {
			&:not(:last-child) {
				margin: {
					right: indents(md);
				}
			}
		}

		@include b-down(md) {
			&:not(:last-child) {
				margin: {
					bottom: indents(lg);
				}
			}
		}

		.icon {
			width: 22px;
			min-width: 22px;
			height: 22px;
			margin: {
				right: 12px;
			}

			color: $violet;
		}

		&.is-active {
			color: $yellow;
		}

		&--phone {
			color: $violet;
			font: {
				weight: 600;
			}

			white-space: nowrap;
		}

		&--big {
			@include font(lg);

			@include b-down(xxs) {
				@include font(xmd);
			}

			.icon {
				width: 34px;
				min-width: 34px;
				height: 34px;
				color: $violet;
			}
		}
	}

	&--top {
		@include b-up(lg) {
			margin: {
				left: auto;
			}
			padding: {
				right: calc(65px + 172px - 60px);
			}
		}

		.menu__link {
			@include b-up(lg) {
				@include font(sm);

				.icon {
					width: 18px;
					height: 18px;
					margin: {
						right: 12px;
					}
				}

				&:not(:last-child) {
					margin: {
						right: indents(xxl);
					}
				}
			}
		}

		@include b-down(md) {
			width: 100%;
			max-width: 100%;
		}
	}

	&--main {
		@include b-up(lg) {
			width: calc(100% - 65px - 172px);
		}
	}

	&--footer {
		padding: {
			top: indents(xl);
			bottom: indents(md);
		}

		@include b-down(md) {
			padding: {
				right: indents(sm);
				left: indents(sm);
			}
		}

		@include b-up(lg) {
			padding: {
				top: indents(lg);
				bottom: indents(lg);
			}
		}
	}

	&--callback {
		.btn {
			padding: 0;
		}

		@include b-down(md) {
			position: absolute;
			top: calc(-70px + 32px / 2);
			right: 20px;

			.btn {
				width: 28px;
				height: 28px;
				border: 0 none;

				&__text {
					display: none;
				}

				.icon {
					width: 28px;
					height: 28px;
					margin: 0;
				}
			}
		}

		@include b-up(lg) {
			padding: {
				left: 65px;
			}

			.btn {
				max-width: 172px;

				.icon {
					display: none;
				}
			}
		}
	}
}
