.footer {
	@extend %d-lg-flex;
	@extend %flex-lg-column-reverse;

	position: relative;
	z-index: 1;
	margin: {
		top: auto;
	}
	background: {
		image: $bg-footer;
	}

	@include b-down(md) {
		padding: {
			bottom: 60px;
		}
	}

	&__main {
		padding: {
			top: indents(xxl);
			bottom: indents(xxl);
		}

		@include b-up(lg) {
			padding: {
				top: indents(xl);
				bottom: indents(xl);
			}
		}
	}

	&__main-row {
		@extend %align-items-center;
	}

	&__logo,
	&__copyright,
	&__phone {
		@extend %col-12;
		@extend %col-lg-4;
		@extend %d-flex;
		@extend %justify-content-center;
	}

	&__logo {
		@extend %justify-content-lg-start;

		@include b-down(md) {
			margin: {
				bottom: indents(xxl);
			}
		}
	}

	&__phone {
		@extend %justify-content-lg-end;

		.menu__link {
			padding: {
				top: 0;
				bottom: 0;
			}
		}
	}

	&__menu {
		@include b-up(lg) {
			@include border(bottom, hsl(0, 0%, 88%));
		}

		@include b-down(md) {
			@include border(top, hsl(0, 0%, 88%));
		}
	}

	&__copyright {
		@include font(md, 'Raleway', 300);

		@extend %align-items-center;
		@extend %justify-content-center;

		color: $gray;
		line-height: 1.2;
		text: {
			align: center;
		}

		@include b-down(md) {
			position: absolute;
			right: 0;
			bottom: 0;
			left: 0;
			height: 60px;
			padding: {
				bottom: indents(md);
			}
		}
	}
}
