%ch-accordion {
	+ .accordion__main {
		.accordion__content {
			@extend %d-block;
		}

		.accordion__header {
			background: transparent;
			color: $violet;
		}
	}

	~ .accordion__btn {
		&::before {
			top: 14px;
			height: 0;
		}
	}
}

.accordion {
	display: block;
	margin: {
		top: indents(xxl);
	}

	&__item {
		@include border(bottom, hsl(0, 0%, 86%));

		position: relative;
	}

	&__header {
		@include transition('background opacity color');
		@include hover;

		@extend .headline--l3;
		@extend %d-block;

		padding: 12px 44px 12px 20px;
		background: hsl(0, 0%, 96%);
		cursor: pointer;

		@include b-up(md) {
			padding: 12px 64px 12px 50px;
		}
	}

	&__content {
		@extend %d-none;

		padding: 12px 44px 12px 20px;

		@include b-up(md) {
			padding: 12px 64px 12px 50px;
		}
	}

	&__btn {
		@extend %d-block;

		position: absolute;
		top: calc(50% - 15px);
		right: 14px;
		width: 30px;
		height: 30px;
		cursor: pointer;

		@include b-up(md) {
			right: 34px;
		}

		&::before,
		&::after {
			content: '';
			position: absolute;
			background: {
				color: $dark;
			}
		}

		&::before {
			@include transition('top height');

			top: 0;
			left: 14px;
			width: 2px;
			height: 30px;
		}

		&::after {
			top: 14px;
			left: 0;
			width: 30px;
			height: 2px;
		}
	}

	&__input {
		&:checked {
			@extend %ch-accordion;
		}
	}
}
