@use 'sass:math';

/* stylelint-disable */
@import '../../../scss/util/variables';
@import '../../../scss/util/functions';
@import '../../../scss/util/mixins';
@import '../../../scss/util/placeholder';
/* stylelint-enable */



































































































































.form {
	.headline {
		max-width: 80%;
		font: {
			size: 20px;
		}

		@include b-up(md) {
			font: {
				size: 30px;
			}
			margin: {
				bottom: 40px;
			}
		}
	}

	&__main {
		position: relative;
	}

	&__item {
		@extend %col-12;
		@extend %col-md-4;

		&:not(:last-child) {
			margin: {
				bottom: 25px;
			}

			@include b-up(md) {
				margin: {
					bottom: 30px;
				}
			}
		}

		&--full {
			@extend %col-md-8;
		}

		&--none {
			&:not(:last-child) {
				margin: {
					bottom: 0;
				}
			}
		}
	}

	.btn {
		width: 100%;
		max-width: none;
	}
}
