.headline {
	@include font(slg, 'Montserrat', 900);

	margin: {
		bottom: indents(lg);
	}

	color: $violet;

	@include b-up(xs) {
		@include font(mxl);
	}

	@include b-up(md) {
		@include font(xl);
	}

	&--l3 {
		@include font(mlg, 'Montserrat', 600);

		@include b-up(md) {
			@include font(lg);
		}
	}
}
