
.partners {
	background: {
		color: $violet;
	}

	&__row {
		@extend %align-items-md-center;
	}

	&__title {
		@extend %col-12;
		@extend %col-md-4;

		@include b-down(sm) {
			margin: {
				bottom: indents(xl);
			}
		}

		.headline {
			color: $white;

			@include b-up(md) {
				margin: {
					bottom: 0;
				}
			}
		}
	}

	&__slider {
		@extend %col-12;
		@extend %col-md-8;

		&.no-touch {
			pointer-events: none;
		}

		@include b-down(sm) {
			padding: 0;
		}
	}

	&__slide {
		@extend %d-flex;
		@extend %justify-content-md-center;
		@extend %align-items-center;

		@include b-down(sm) {
			width: 200px;
			padding: {
				right: indents(lg);
				left: indents(lg);
			}

			&:not(.is-active) {
				opacity: .4;
			}
		}
	}

	&__img {
		width: auto;
		max-width: 100%;
		height: auto;
		max-height: 48px;

		@include b-up(md) {
			max-height: 42px;
		}
	}
}
